:root {
	// Maximum Width including browser scrollbar
	--max-width: calc(100vw - var(--scroll-width));

	// Sizes
	--grid-gap: 24px;
	--global-radius: 12px;

	--navigation-h: 80px;


	// Typography
	--font-primary: 'Inter', sans-serif;
	--font-heading: 'Inter', sans-serif;

	// Colors
	--bg-color: #fff;
	--color-grey: #ECECEC;

	--color-grey-socials: #929292;

	--color-primary: #F3E8CE;
	--color-secondary: #ECECEC;


	--color-white: #fff;


	--color-link: #2d2dba;
	--color-link-hover: #14142b;

	--color-text: #000;
	--color-text-white: #fff;

	--color-dark: #0C0C0C;
	--color-dark-hover: #393636;


}

@media (max-width: 767px) {
	:root {
		--navigation-h: 64px;
	}
}


// Dark Theme
[data-color-scheme="dark"] {
	&:root {
		--bg-color: #0F0F0F;

		--color-text: #fff;
		--color-text-white: #fff;

		--color-white: #fff;

		--color-grey-socials: #818181;

		--color-grey-bg:#242424;


	}
}

// Breakpoints
$grid-breakpoints: (
	mobile: 575,
	mobile-large: 640,
	tablet-small:768,
	tablet: 1024,
	tiny--desktop: 1280,
	small-laptop: 1366,
	medium-laptop: 1440,
	FULLHD: 1920,
	WQHD: 2440,
);
