.pika-single {
	z-index: 9999;
	display: block;
	position: relative;
	color: #333;
	background: #fff;
	border: 1px solid #ccc;
	border-bottom-color: #bbb;
	font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

/*
clear child float (pika-lendar), using the famous micro clearfix hack
http://nicolasgallagher.com/micro-clearfix-hack/
*/
.pika-single:before,
.pika-single:after {
	content: " ";
	display: table;
}

.pika-single:after {
	clear: both
}

.pika-single.is-hidden {
	display: none;
}

.pika-single.is-bound {
	position: absolute;
	box-shadow: 0 5px 15px -5px rgba(0, 0, 0, .5);
}

.pika-lendar {
	float: left;
	width: 240px;
	margin: 8px;
}

.pika-title {
	position: relative;
	text-align: center;
}

.pika-label {
	display: inline-block;
	position: relative;
	z-index: 9999;
	overflow: hidden;
	margin: 0;
	padding: 5px 3px;
	font-size: 14px;
	line-height: 20px;
	font-weight: bold;
	background-color: #fff;
}

.pika-title select {
	cursor: pointer;
	position: absolute;
	z-index: 9998;
	margin: 0;
	left: 0;
	top: 5px;
	opacity: 0;
}

.pika-prev,
.pika-next {
	display: block;
	cursor: pointer;
	position: relative;
	outline: none;
	border: 0;
	padding: 0;
	width: 20px;
	height: 30px;
	/* hide text using text-indent trick, using width value (it's enough) */
	text-indent: 20px;
	white-space: nowrap;
	overflow: hidden;
	background-color: transparent;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: 75% 75%;
	opacity: .5;
}

.pika-prev:hover,
.pika-next:hover {
	opacity: 1;
}

.pika-prev,
.is-rtl .pika-next {
	float: left;
	background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAeCAYAAAAsEj5rAAAAUklEQVR42u3VMQoAIBADQf8Pgj+OD9hG2CtONJB2ymQkKe0HbwAP0xucDiQWARITIDEBEnMgMQ8S8+AqBIl6kKgHiXqQqAeJepBo/z38J/U0uAHlaBkBl9I4GwAAAABJRU5ErkJggg==);
}

.pika-next,
.is-rtl .pika-prev {
	float: right;
	background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAeCAYAAAAsEj5rAAAAU0lEQVR42u3VOwoAMAgE0dwfAnNjU26bYkBCFGwfiL9VVWoO+BJ4Gf3gtsEKKoFBNTCoCAYVwaAiGNQGMUHMkjGbgjk2mIONuXo0nC8XnCf1JXgArVIZAQh5TKYAAAAASUVORK5CYII=);
}

.pika-prev.is-disabled,
.pika-next.is-disabled {
	cursor: default;
	opacity: .2;
}

.pika-select {
	display: inline-block;
}

.pika-table {
	width: 100%;
	border-collapse: collapse;
	border-spacing: 0;
	border: 0;
}

.pika-table th,
.pika-table td {
	width: 14.285714285714286%;
	padding: 0;
}

.pika-table th {
	color: #999;
	font-size: 12px;
	line-height: 25px;
	font-weight: bold;
	text-align: center;
}

.pika-button {
	cursor: pointer;
	display: block;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	outline: none;
	border: 0;
	margin: 0;
	width: 100%;
	padding: 5px;
	color: #666;
	font-size: 12px;
	line-height: 15px;
	text-align: right;
	background: #f5f5f5;
	height: initial;
}

.pika-week {
	font-size: 11px;
	color: #999;
}

.is-today .pika-button {
	color: #33aaff;
	font-weight: bold;
}

.is-selected .pika-button,
.has-event .pika-button {
	color: #fff;
	font-weight: bold;
	background: #33aaff;
	box-shadow: inset 0 1px 3px #178fe5;
	border-radius: 3px;
}

.has-event .pika-button {
	background: #005da9;
	box-shadow: inset 0 1px 3px #0076c9;
}

.is-disabled .pika-button,
.is-inrange .pika-button {
	background: #D5E9F7;
}

.is-startrange .pika-button {
	color: #fff;
	background: #6CB31D;
	box-shadow: none;
	border-radius: 3px;
}

.is-endrange .pika-button {
	color: #fff;
	background: #33aaff;
	box-shadow: none;
	border-radius: 3px;
}

.is-disabled .pika-button {
	pointer-events: none;
	cursor: default;
	color: #999;
	opacity: .3;
}

.is-outside-current-month .pika-button {
	color: #999;
	opacity: .3;
}

.is-selection-disabled {
	pointer-events: none;
	cursor: default;
}

.pika-button:hover,
.pika-row.pick-whole-week:hover .pika-button {
	color: #fff;
	background: #ff8000;
	box-shadow: none;
	border-radius: 3px;
}

/* styling for abbr */
.pika-table abbr {
	border-bottom: none;
	cursor: help;
}

.pika-single {
	color: #3c4858
}

.pika-button {
	color: #3c4858;
	background: #fff
}

.sib-is-today .pika-button {
	color: #0092ff
}

.sib-is-selected .pika-button {
	color: #fff
}

.sib-is-today:hover .pika-button {
	color: #fff
}

.sib-is-selected .pika-button, .sib-has-event .pika-button {
	background: #0092ff
}

.pika-button:hover, .pika-row.sib-pick-whole-week:hover .pika-button {
	background: #0092ff
}

.pika-table abbr {
	cursor: default;
	color: #8390a4;
	text-decoration: none
}

.pika-label {
	font-size: 0
}

.pika-title select {
	position: initial;
	opacity: 1;
	z-index: inherit
}

.sib-sms-tooltip {
	margin-left: 8px;
	position: relative
}

.sib-sms-tooltip__box {
	display: none;
	border-radius: 5px;
	color: #fff;
	background-color: #333;
	position: absolute;
	bottom: 100%;
	width: 200px;
	padding: 5px;
	right: calc(100% - 60px);
	font-size: 12px
}

.sib-sms-tooltip:hover .sib-sms-tooltip__box {
	display: block
}

.sib-sms-tooltip__icon {
	border-radius: 50%;
	color: #4da6e0;
	width: 24px;
	height: 24px;
	padding: 3px;
	font-weight: 700;
	font-style: normal;
	font-size: 24px
}

.sib-container--medium.sib-container--horizontal .sib-sms-tooltip, .sib-container--small .sib-sms-tooltip {
	position: absolute;
	bottom: 0;
	right: 0;
	border-radius: 100%;
	height: 18px;
	width: 18px;
	margin: 12px 8px;
	background: #687484;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center
}

.sib-container--medium.sib-container--horizontal .sib-sms-tooltip__icon, .sib-container--small .sib-sms-tooltip__icon {
	color: #fff;
	font-size: 16px
}

.sib-sms-input-wrapper {
	display: flex;
	align-items: center;
	position: relative
}

.sib-sms-field .form__label-row--horizontal {
	flex-wrap: wrap
}

