.sib-sms-select {
	display: flex;
	width: 100%;
	font-size: 14px;
	position: relative
}

.sib-container--medium.sib-container--horizontal .sib-sms-select, .sib-container--small .sib-sms-select {
	flex-direction: column
}

.sib-sms-select__title {
	display: flex;
	position: relative;
	align-items: center;
	background: #fff;
	border: 1px solid #c0ccda;
	border-radius: 3px;
	box-sizing: border-box;
	cursor: pointer;
	font-size: 14px;
	height: 3em;
	padding: 8px 16px 8px 8px;
	margin-right: 8px;
	text-align: left;
	width: 100%;
	max-width: 60px
}

.sib-sms-select__title::after {
	content: "";
	display: block;
	position: absolute;
	right: 5px;
	width: 0;
	height: 0;
	border-left: 5px solid rgba(0, 0, 0, 0);
	border-right: 5px solid rgba(0, 0, 0, 0);
	border-top: 5px solid #000
}

.sib-sms-select__title > .sib-sms-select__label-text {
	display: none
}

.sib-container--medium.sib-container--horizontal .sib-sms-select__title, .sib-container--small .sib-sms-select__title {
	max-width: 100%;
	margin-right: 0;
	margin-bottom: .5rem
}

.sib-container--medium.sib-container--horizontal .sib-sms-select__title::after, .sib-container--small .sib-sms-select__title::after {
	content: "";
	display: block;
	position: absolute;
	right: 8px;
	width: 0;
	height: 0;
	border-left: 5px solid rgba(0, 0, 0, 0);
	border-right: 5px solid rgba(0, 0, 0, 0);
	border-top: 5px solid #000
}

.sib-container--medium.sib-container--horizontal .sib-sms-select__title > .sib-sms-select__label-text, .sib-container--small .sib-sms-select__title > .sib-sms-select__label-text {
	display: block
}

.sib-sms-select__list {
	background: #fff;
	border: 1px solid #c0ccda;
	border-radius: 3px;
	box-sizing: border-box;
	display: none;
	height: 0;
	list-style: none;
	margin: 8px 0 0 0;
	opacity: 0;
	padding: 0;
	position: absolute;
	width: 100%;
	z-index: 999
}

.sib-sms-select__list.sib-is-open {
	display: block;
	height: auto;
	max-height: 250px;
	overflow: scroll;
	opacity: 1
}

.sib-sms-select__list li {
	display: flex;
	align-items: center;
	border-bottom: 1px solid #c0ccda;
	cursor: pointer;
	padding: 8px;
	font-size: 14px
}

.sib-sms-select__list li:hover, .sib-sms-select__list li.sib-is-selected {
	background-color: #8ed8fd
}

.sib-sms-select__number-input {
	display: flex;
	width: 100%;
	height: 3em;
	border: 1px solid #c0ccda;
	border-radius: 3px;
	background: #fff
}

.sib-sms-select__calling-code {
	height: 100%;
	max-width: 70px;
	padding: 8px;
	border-top: 0;
	border-right: 1px solid #c0ccda;
	border-bottom: 0;
	border-left: 0;
	border-radius: 3px 0 0 3px;
	line-height: 1.8em;
	text-align: center;
	font-size: inherit;
	color: inherit;
	outline: none
}

.sib-container--medium.sib-container--horizontal .sib-sms-select__calling-code, .sib-container--small .sib-sms-select__calling-code {
	max-width: 50px;
	padding: 8px 4px
}

.sib-sms-select__phone-number {
	width: calc(100% - 70px);
	height: 100%;
	padding: 8px;
	border-radius: 3px;
	border: 0;
	outline: 0;
	font-size: inherit
}

.sib-sms-select__label-text {
	margin-left: 8px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis
}

@media screen and (max-width: 480px) {
	.sib-sms-select {
		display: block
	}
	.sib-sms-select__title {
		display: inline-block;
		max-width: 100%;
		margin-bottom: 10px
	}
	.sib-container--small .sib-sms-select__title {
		display: flex
	}
	.sib-container--small .sib-sms-select__title .sib-flag {
		margin-left: 0;
		bottom: 0
	}
	.sib-container--small .sib-sms-select__title .sib-sms-select__label-text {
		margin-left: .5rem;
		padding-bottom: 0
	}
