@use "../include" as *;

.btn {
	// Sizes
	height: 64px;
	border-radius: 300px;
	padding: 0 20px;
	font-size: 15px;
	min-width: 220px;
	gap: 4px;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	font-weight: 700;
	line-height: 1;
	text-align: center;
	cursor: pointer;
	text-transform: uppercase;
	white-space: nowrap;
	font-family: var(--font-primary);
	@include transition;

	.icon:not(.progress-indicator__icon) {
		display: flex;
		justify-content: center;
		align-items: center;
		backface-visibility: hidden;
		padding-bottom: 1px;
		@include transition;
	}

	&:hover {
		.icon {
			transform: translateX(5px);
		}
	}


	@media (max-width: 640px) {
		height: 56px;
		min-width: 166px;
		font-size: 13px;
	}
}


// Primary Button
[data-color-scheme="light"] {
	.btn-primary {
		// Colors
		background-color: var(--color-dark);
		border: 1px solid var(--color-dark);
		color: var(--color-text-white);

		&:hover,
		&:focus,
		&:active {
			background-color: var(--color-dark-hover);
			border-color: var(--color-dark-hover);
			color: var(--color-text-white);
		}
	}
}

[data-color-scheme="dark"] {
	.btn-primary {
		// Colors
		background-color: var(--color-dark);
		border: 1px solid var(--color-dark);
		color: var(--color-text-white);
		position: relative;

		&::before {
			content: "";
			position: absolute;
			inset: 0;
			padding: 2px; /* Adjust this to control the border thickness */
			border-radius: 50px;
			background: conic-gradient(from 180deg at 50% 50%, #72E5BA -0.14deg, #72E5BA 31.76deg, #FFF965 60.49deg, #FFA082 149.73deg, #FF746E 209.69deg, #A388FA 300.82deg, #A388FA 329.2deg, #72E5BA 359.86deg, #72E5BA 391.76deg);
			-webkit-mask: linear-gradient(#000 0 0) content-box,
			linear-gradient(#000 0 0);
			-webkit-mask-composite: xor;
			mask-composite: exclude;
			pointer-events: none; /* Ensures the border doesn't interfere with interactions */
		}

		&:hover,
		&:focus,
		&:active {
			background-color: var(--color-dark);
			color: var(--color-text-white);
		}
	}
}

.btn-primary-dark {
	// Colors
	background-color: var(--color-dark);
	border: 1px solid var(--color-dark);
	color: var(--color-text-white);
	position: relative;

	&::before {
		content: "";
		position: absolute;
		inset: 0;
		padding: 2px; /* Adjust this to control the border thickness */
		border-radius: 50px;
		background: conic-gradient(from 180deg at 50% 50%, #72E5BA -0.14deg, #72E5BA 31.76deg, #FFF965 60.49deg, #FFA082 149.73deg, #FF746E 209.69deg, #A388FA 300.82deg, #A388FA 329.2deg, #72E5BA 359.86deg, #72E5BA 391.76deg);
		-webkit-mask: linear-gradient(#000 0 0) content-box,
		linear-gradient(#000 0 0);
		-webkit-mask-composite: xor;
		mask-composite: exclude;
		pointer-events: none; /* Ensures the border doesn't interfere with interactions */
	}

	&:hover,
	&:focus,
	&:active {
		background-color: var(--color-dark);
		color: var(--color-text-white);
	}
}


// Secondary Button
[data-color-scheme="light"] {
	.btn-secondary {
		// Colors
		background-color: var(--color-secondary);
		border: 1px solid var(--color-secondary);
		color: var(--color-dark);

		&:hover,
		&:focus,
		&:active {
			background-color: var(--color-primary);
			border-color: var(--color-primary);
			color: var(--color-dark);
		}
	}
}

[data-color-scheme="dark"] {
	.btn-secondary {
		color: var(--color-grey-bg);
		background-color: var(--color-white);
		border-color: var(--color-white);

		&:hover,
		&:focus,
		&:active {
			background-color: var(--color-dark-hover);
			border-color: var(--color-dark-hover);
			color: var(--color-white);
		}
	}
}


// Button Group
.btn-group {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	gap: 24px;

	@media (max-width: 768px) {
		gap: 13px;


	}
}

// Кнопка минтинга NFT
.btn-mint {
	transition: all 0.3s ease;

	&.minting {
		opacity: 0.7;
		cursor: not-allowed;
		animation: pulse 1.5s infinite;
	}
}

@keyframes pulse {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(1.05);
	}
	100% {
		transform: scale(1);
	}
}

// Контейнер для кнопок в шапке
.header-actions {
	display: flex;
	align-items: center;
	gap: 10px;
	margin-right: 15px;

	.btn {
		min-width: auto;
		height: 40px;
		padding: 0 15px;
		font-size: 12px;
	}
}
