@use "../include" as *;

.site-footer {
	min-height: 80px;
	padding-left: 40px;
	padding-right: 40px;
	display: flex;
	justify-content: space-between;
	max-width: var(--max-width);
	overflow-x: hidden;
	margin-left: auto;
	margin-right: auto;
	@media (min-width: 1440px) {
		max-width: 1440px;
	}

	&__left {
		display: flex;
		align-items: center;
		width: 25%;
	}

	&__center {
		width: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&__right {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		width: 25%;
	}

	.copyright {
		font-style: normal;
		font-weight: 500;
		font-size: 18px;
		line-height: 1.45;
		color: var(--color-grey-socials);
	}

	.policy-link {
		font-weight: 500;
		font-size: 18px;
		line-height: 1.45;
		color: var(--color-grey-socials);
		text-decoration: underline;
		text-underline-offset: 5px;
		text-decoration-thickness: 1px;
		@include transition;

		&:hover {
			color: var(--color-text);
		}
	}

	.socials {
		justify-content: flex-end;
	}

	@media (max-width: 767px) {
		padding-left: var(--grid-gap);
		padding-right: var(--grid-gap);
		flex-wrap: wrap;
		.policy-link {
			font-weight: 600;
		}

		&__left {
			width: 50%;
			order: 0;
		}
		&__right {
			width: 50%;
			order: 1;
		}
		&__center {
			width: 100%;
			order: 2;
			padding: 24px 0;
			text-align: center;
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}
}
