@use "../include" as *;

.site-header {
	position: absolute;
	height: var(--navigation-h);
	left: 0;
	top: 0;
	right: 0;
	padding-left: 40px;
	padding-right: 40px;
	display: flex;
	justify-content: space-between;
	max-width: var(--max-width);
	overflow-x: hidden;
	margin-left: auto;
	margin-right: auto;
	@media (min-width: 1440px) {
		max-width: 1440px;
	}

	&__left {
		display: flex;
		align-items: center;
		width: 25%;
	}

	&__center {
		width: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&__right {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		width: 25%;

		.btn-toggle-nav {
			display: none;
		}
	}

	.site-logo {
		img {
			height: 32px;
			width: auto;
			display: block;
			object-fit: contain;
		}
	}

	.site-nav {
		ul {
			display: flex;
			justify-content: center;
			align-items: center;
			gap: var(--grid-gap);

			li {
				a {
					font-style: normal;
					font-weight: 500;
					font-size: 18px;
					line-height: 1.44;
					color: var(--color-text);
					padding: 4px 0;
					@include hover-underline();

					&[href="#"] {
						color: var(--color-grey-socials);
						pointer-events: none;
					}

				}
			}
		}
	}

	.socials {
		justify-content: flex-end;
	}

	@media (max-width: 767px) {
		padding-left: 18px;
		padding-right: 18px;
		.site-logo {
			img {
				height: 28px;
			}
		}
		.socials {
			display: none;
		}
		.site-nav {
			display: none;
		}
		&__center {
			display: none;
		}
		&__left {
			width: 50%;
		}
		&__right {
			width: 50%;

			.btn-toggle-nav {
				position: relative;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				cursor: pointer;
				width: 24px;
				height: 24px;
				color: var(--color-text);
				svg {
					path {
						stroke: currentColor;
					}
				}
			}


		}
	}
}

.site-header__mobile {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	max-width: var(--max-width);
	height: 100vh;
	height: 100dvh;
	background-color: var(--bg-color);
	z-index: 50;
	opacity: 0;
	pointer-events: none;
	@include transition;

	@media (min-width: 768px) {
		display: none;
	}

	&.active {
		opacity: 1;
		pointer-events: all;
	}


	&-top {
		height: var(--navigation-h);
		padding-left: 18px;
		padding-right: 18px;
		display: flex;
		justify-content: flex-end;
		align-items: center;

		.btn-close-nav {
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			cursor: pointer;
			width: 24px;
			height: 24px;
			color: var(--color-text);
			svg {
				path {
					stroke: currentColor;
				}
			}
		}
	}

	&-body {
		height: calc(100vh - var(--navigation-h));
		padding-top: 66px;
		padding-bottom: 66px;
		padding-left: 40px;
		padding-right: 40px;
		overflow-y: auto;
		-webkit-overflow-scrolling: touch;

		.site-nav {
			ul {
				li {
					margin-bottom: 16px;

					&:last-child {
						margin-bottom: 0;
					}

					a {
						font-style: normal;
						font-weight: 500;
						font-size: 32px;
						line-height: 1.21;
						color: var(--color-text);
						@include hover-underline();
					}
				}
			}
		}

		.socials {
			margin-top: 40px;

			a {
				svg {
					height: 30px;
					width: auto;
					display: block;
				}
			}
		}
	}
}
