@use "../include" as *;

.page-hiring {
	min-height: min(calc(100vh - 80px), 1271px);
	padding-top: clamp-property(64, 120);
	padding-bottom: clamp-property(80, 120);

	.section-hero {
		padding-top: 72px;
		@media (min-width: 1440px) {
			padding-top: 125px;
		}
		@media (max-width: 1199px) {
			padding-top: 56px;
		}
		@media (max-width: 767px) {
			padding-top: 48px;
		}

		.page-title {
			picture, img {
				display: inline;
				width: clamp-property(32, 55);
				height: auto;
				object-fit: contain;
				transform: translateY(-9%) translateX(6%);
				margin-right: 1px;
			}
		}

		br{
			display: none;
			@media (max-width: 565px) {
				display: block;
			}
		}

		.container {
			display: flex;
			flex-direction: column;
			gap: 24px;
		}

		.btn-group {
			display: flex;
			padding-top: 8px;
			@media (max-width: 480px) {
				.btn {
					width: 100%;
				}
			}
		}
	}

	.section-positions {
		padding-top: 72px;
		@media (max-width: 1199px) {
			padding-top: 56px;
		}
		@media (max-width: 767px) {
			padding-top: 48px;
		}

		.section-title {
			margin-bottom: 24px;
		}

		.position-list {
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: flex-start;
			gap: 16px;

			&__item {
				padding-top: 8px;
				padding-bottom: 8px;
				gap: 10px;
				display: flex;
				flex-direction: column;
				justify-content: flex-start;
				align-items: flex-start;
				@include transition;

				.job-title {
					letter-spacing: -0.36px;
					font-weight: 500;
					@include hover-underline();
				}

				.location {
					font-size: clamp-property(16, 18);
				}

				&:hover {
					padding-left: 16px;
				}
			}
		}
	}

}
