@use "../include" as *;

.page-personality {
	.section-hero {
		padding-top: clamp-property(104, 200);
		.section-head {
			display: flex;
			flex-direction: column;
			align-items: center;
			text-align: center;
			max-width: 800px;
			margin-left: auto;
			margin-right: auto;
		}

		.page-title {
			margin-bottom: 32px;
			text-wrap: balance;
		}

		.editor {
			margin-bottom: 40px;
			text-wrap: balance;
		}


		.btn-group {
			.btn {
				flex: 1;
			}
		}

		.aura-showcase {
			display: flex;
			justify-content: center;
			margin-top: 42px;
		}

		@media (max-width: 767px) {
			.page-title {
				margin-bottom: 24px;
			}
			.editor {
				margin-bottom: 30px;
			}
		}

	}

	.section-about {
		padding-top: clamp-property(75, 120);
		padding-bottom: clamp-property(56, 120);
		text-align: center;

		.section-title {
			margin-bottom: 40px;

			picture, img {
				display: inline;
				width: clamp-property(32, 55);
				height: auto;
				object-fit: contain;
				transform: translateY(-9%) translateX(6%);
				margin-right: 1px;
			}
		}

		.editor {
			max-width: 720px;
			margin-left: auto;
			margin-right: auto;
		}

		.preview-img {
			width: 100%;
			max-width: 740px;
			margin-left: auto;
			margin-right: auto;
			margin-top: clamp-property(32, 80);
		}

		@media (max-width: 767px) {
			.section-title {
				margin-bottom: 32px;
			}
			.editor {

			}
		}
	}

	.section-ai-soul {
		padding-top: clamp-property(56, 120);
		padding-bottom: clamp-property(56, 120);
		text-align: center;

		.section-title {
			margin-bottom: 40px;
		}

		.editor {
			max-width: 740px;
			margin-left: auto;
			margin-right: auto;
		}

		.virtues-cols {
			margin-top: clamp-property(32, 68);
			margin-bottom: clamp-property(32, 68);
			display: flex;
			max-width: 740px;
			margin-left: auto;
			margin-right: auto;
			gap: 40px;
			flex-wrap: wrap;

			.virtue-col {
				flex: 1;

				ul {
					li {
						text-align: left;
						font-style: normal;
						padding-bottom: 8px;
						font-weight: 700;
						font-size: 24px;
						line-height: 1.5;
						color: var(--color-text);
					}
				}
			}
		}

		.aura-grid {
			max-width: 840px;
			margin-left: auto;
			margin-right: auto;
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			margin-top: clamp-property(24, 80);
			gap: 40px;

			.aura-card {
				display: flex;
				flex-direction: column;

				&__img {
					display: flex;
					justify-content: center;
					align-items: center;
					max-width: 320px;
					margin-left: auto;
					margin-right: auto;
					margin-bottom: 24px;
				}

				&__title {
					text-align: center;
					margin-bottom: 10px;
					font-style: normal;
					font-weight: 700;
					font-size: 24px;
					line-height: 1.5;
					color: var(--color-text);
				}

				&__description {
					font-style: normal;
					font-weight: 400;
					font-size: 24px;
					line-height: 1.5;
					text-align: center;
					color: var(--color-text);
				}
			}
		}

		.btn-group {
			margin-top: clamp-property(32, 80);
			display: flex;
			justify-content: center;
			align-items: center;
		}

		@media (max-width: 1024px) {
			.section-title {
				margin-bottom: 32px;
			}
		}
		@media (max-width: 767px) {
			.aura-grid {
				grid-template-columns: repeat(1, 1fr);

			}
		}
	}

	.section-community {
		padding-top: clamp-property(56, 120);
		padding-bottom: clamp-property(56, 120);
		text-align: center;

		.section-title {
			margin-bottom: 40px;
		}

		.section-caption {
			margin-top: clamp-property(40, 80);
			padding-top: clamp-property(32, 56);
			padding-bottom: clamp-property(32, 56);
			padding-left: clamp-property(24, 56);
			padding-right: clamp-property(24, 56);
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			border-radius: clamp-property(24, 32);
			max-width: 800px;
			margin-left: auto;
			margin-right: auto;
			width: 100%;
			line-height: 1.38;

			&::before {
				content: "";
				position: absolute;
				inset: 0;
				padding: 6px; /* Adjust this to control the border thickness */
				border-radius: 50px;
				background: linear-gradient(93.98deg, #EA4E47 0%, #EBCE5B 31.67%, #66E57B 64.87%, #7E5DE9 100%);

				/* Masking technique to create a border effect */
				-webkit-mask: linear-gradient(#000 0 0) content-box,
				linear-gradient(#000 0 0);
				-webkit-mask-composite: xor;
				mask-composite: exclude;

				pointer-events: none; /* Ensures the border doesn't interfere with interactions */
			}

			@media (max-width: 782px) {
				font-size: 18px;
			}
		}

		.editor {
			max-width: 740px;
			margin-left: auto;
			margin-right: auto;
		}

		.community-list {
			margin-top: clamp-property(40, 80);
			max-width: 740px;
			margin-left: auto;
			margin-right: auto;

			&__item {
				display: flex;
				align-items: flex-start;
				gap: clamp-property(12, 24);
				margin-bottom: clamp-property(24, 32);

				&:last-child {
					margin-bottom: 0;
				}

				&-icon {
					img {
						width: clamp-property(24, 48);
						min-width: clamp-property(24, 48);
						height: auto;
					}
				}

				&-info {
					display: flex;
					flex-direction: column;
					justify-content: flex-start;
					gap: 8px;
				}

				&-title {
					text-align: left;
				}

				.editor {
					text-align: left;
					font-size: 16px;
				}

			}
		}

		@media (max-width: 1024px) {
			.section-title {
				margin-bottom: 32px;
			}

		}
	}

	.section-roadmap {
		padding-top: clamp-property(56, 120);
		padding-bottom: clamp-property(56, 120);

		.section-title {
			text-align: center;
		}

		.roadmap-grid {
			margin-top: clamp-property(40, 80);
			gap: clamp-property(16, 24);
			display: grid;
			grid-template-columns: repeat(3, 1fr);

			.roadmap-item {
				background-color: var(--color-primary);
				padding: clamp-property(24, 40);
				border-radius: clamp-property(24, 32);

				.roadmap-title {
					font-size: clamp-property(18, 26);
					font-weight: 700;
				}

				.roadmap-date {
					font-size: clamp-property(18, 26);
					font-weight: 400;
				}

				.editor {
					margin-top: clamp-property(24, 32);
					font-size: clamp-property(18, 16);
				}
			}
		}

		@media (max-width: 1024px) {
			.roadmap-grid {
				grid-template-columns: repeat(1, 1fr);


			}
		}
		@media (max-width: 782px) {
			.roadmap-grid {
				.roadmap-item {
					.editor {
						font-size: 16px;
					}
				}
			}
		}

		[data-color-scheme="dark"] & {
			.roadmap-grid {
				.roadmap-item {
					background-color: var(--color-grey-bg);
				}
			}
		}
	}

	.section-vision {
		padding-top: clamp-property(56, 120);
		padding-bottom: clamp-property(56, 120);

		.editor {
			max-width: 740px;
			margin-left: auto;
			margin-right: auto;
		}

		.section-head {
			display: flex;
			flex-direction: column;
			align-items: center;
			text-align: center;
			margin-left: auto;
			margin-right: auto;
		}

		.section-title {
			margin-bottom: 40px;
		}

		.editor {
			margin-bottom: 40px;
		}

		@media (max-width: 1024px) {
			.section-title {
				margin-bottom: 32px;
			}

		}
	}

	.section-genesis-edition {
		.section-title {
			font-size: 32px;
			text-align: center;
			text-wrap: balance;
		}

		.ge-grid {
			display: flex;
			flex-wrap: wrap;
			gap: 24px;
			margin-top: 32px;

			.ge-grid-card--large {
				display: flex;
				background-color: #CECECE;
				border-radius: 32px;
				overflow: hidden;

				.col-product {
					width: 50%;

					img {
						width: 100%;
						height: 100%;
						display: block;
						object-fit: contain;
					}
				}

				.col-info {
					display: flex;
					justify-content: center;
					align-items: center;
					width: 50%;
					padding: clamp-property(20, 40) clamp-property(20, 40);

					.card-title {
						color: var(--color-dark);
						margin-bottom: 16px;
						text-wrap: balance;
					}

					.card-description {
						color: var(--color-dark);
						margin-bottom: 32px;
					}
				}
			}

			.ge-grid-card {
				flex: 1;
				background-color: #CECECE;
				border-radius: 32px;
				padding: 40px;
				min-height: 176px;
				display: flex;
				align-items: center;
				justify-content: flex-start;
				gap: 16px;

				img {
					width: 48px;
					min-width: 48px;
					display: block;
					height: auto;
					object-fit: contain;
				}

				.caption {
					color: var(--color-dark);
					font-style: normal;
					font-weight: 700;
					font-size: 24px;
					line-height: 1.5;
				}
			}
		}

		@media (max-width: 1024px) {
			.ge-grid {
				.ge-grid-card--large {
					flex-direction: column;

					.col-product {
						width: 100%;
					}

					.col-info {
						width: 100%;
					}
				}

				.ge-grid-card {
					width: 100%;
					flex: unset;
				}
			}
		}
		@media (max-width: 565px) {
			.ge-grid {
				.ge-grid-card {
					min-height: unset;
					padding: 20px;

					img {
						min-width: 32px;
						width: 32px;
					}

					.caption {
						font-size: 20px;
					}
				}
			}
		}
	}
}
