@use "../include" as *;

.socials {
	display: flex;
	align-items: center;
	gap: var(--grid-gap);

	a {
		display: flex;
		justify-content: center;
		align-items: center;

		svg {
			width: 32px;
			min-width: 32px;
			height: auto;
			display: block;
			object-fit: contain;
			path {
				fill: var(--color-grey-socials);
				@include transition;
			}
		}


		@include hover{
			svg {
				path {
					fill: var(--color-text);
				}
			}
		}
	}

	@media (max-width: 767px) {
		gap: 16px;
		a {
			svg {
				width: 24px;
				min-width: 24px;
			}
		}
	}
}
