.sib-form {
	.sib-form-message-panel {
		border-radius: 16px;
		max-width: 800px;
		margin-left: auto;
		margin-right: auto;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		font-size: 18px;

		&.sib-form-message-panel--active {
			margin-top: 24px;
			display: flex;
		}

		&#error-message {
			background-color: #f8d7da;
			border-color: #f5c6cb;
			color: #721c24;
		}

		&#success-message {
			background-color: #d4edda;
			border-color: #c3e6cb;
			color: #155724;
		}

		[data-color-scheme="dark"] & {
			&#error-message {
				background-color: #4a1c1f; /* Dark red */
				border-color: #842029; /* Deep red border */
				color: #f8d7da; /* Light red text */
			}

			&#success-message {
				background-color: #1c3b1f; /* Dark green */
				border-color: #237a3b; /* Deep green border */
				color: #d4edda; /* Light green text */
			}

		}

	}
}

#sib-form {
	.form-inner {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		gap: 16px;

		#EMAIL {
			min-width: 320px;
		}
	}

	.sib-captcha {
		margin-top: 25px;
		display: flex;
		justify-content: center;
	}


	@media (max-width: 767px) {
		.form-inner {
			.sib-form-block {
				width: 100%;
			}

			#EMAIL {
				width: 100%;
				min-width: unset;
			}

			.btn {
				width: 100%;
			}
		}
	}
}



