@use "../include" as *;

// Fluid-responsive font-size calculator
// @link https://websemantics.uk/tools/responsive-font-calculator/
// clamp browser width: from 575px to 1440px

@include headings {
	font-family: var(--font-heading);
	font-weight: 700;
	line-height: 1.1875;
	min-height: 0vw; // safari fix
	margin: 0;
	a {
		color: inherit;
	}
}

h1,
.h1 {
	font-size: clamp-property(32, 64, 375);
}

h2,
.h2 {
	font-size: clamp-property(32, 60);
}

h3,
.h3 {
	font-size: clamp-property(26, 48);
}

.h3-5 {
	font-size: clamp-property(24, 38);
}

h4,
.h4 {
	font-size: clamp-property(20, 26);
}

h5,
.h5 {
	font-size: clamp-property(19, 24);
}

h6,
.h6 {
	font-size: clamp-property(18, 22);
}
