@use "../include" as *;

body {
	font-family: var(--font-primary);
	font-size: clamp-property(18, 26);
	line-height: 1.38;
	color: var(--color-text);

	p {
		font-size: inherit;
		line-height: inherit;
	}
}

.editor {
	font-size: inherit;
	line-height: inherit;

	p {
		margin-bottom: 1.05em;
		font-size: inherit;
		line-height: inherit;

		&:first-child {
			margin-top: 0;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	// 80%
	&.text-sm {
		font-size: clamp-property(calc(18 * 0.8), calc(26 * 0.8));
	}

	// 120%
	&.text-lg {
		font-size: clamp-property(calc(18 * 1.44), calc(26 * 1.2));
	}

	> *:last-child {
		margin-bottom: 0;
	}

	@include headings {
		margin-top: 4rem;
		margin-bottom: 1.75rem;
		&:first-child {
			margin-top: 0;
		}
	}

	h1,
	.h1 {
		font-size: clamp-property(28, 56);
	}

	h2,
	.h2 {
		font-size: clamp-property(24, 36);
	}

	h3,
	.h3 {
		font-size: clamp-property(22, 32);
	}

	.h3-5 {
		font-size: clamp-property(20, 30);
	}

	h4,
	.h4 {
		font-size: clamp-property(20, 28);
	}

	h5,
	.h5 {
		font-size: clamp-property(19, 26);
	}

	h6,
	.h6 {
		font-size: clamp-property(18, 22);
	}


	a:not([class]) {
		color: var(--color-link);
		text-decoration: unset;
		text-decoration-color: transparent;
		@include hover {
			color: var(--color-link-hover);
			text-decoration-color: var(--color-link-hover);
		}
	}

}



