@use "../include" as *;

.modal {
	position: fixed;
	z-index: 999;
	opacity: 0;
	pointer-events: none;

	&.is-open {
		opacity: 1;
		pointer-events: all;
	}
}

.modal__container {
	margin: auto;
	min-height: 100%;
	text-align: center;
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	padding: 0 8px;
	box-sizing: border-box;
	@include transition;

	&:before {
		content: "";
		display: inline-block;
		height: 100%;
		vertical-align: middle;
	}

	.modal-inner {
		width: 860px;
		border: 2px solid var(--color-grey-socials);
		padding-top: clamp-property(32, 60);
		padding-bottom: clamp-property(32, 60);
		padding-left: clamp-property(20, 30);
		padding-right: clamp-property(20, 30);
		background-color: var(--bg-color);
		max-width: calc(100% - 20px);
		border-radius: clamp-property(16, 32);
		position: relative;
		z-index: 150;
		margin-top: 65px;
		margin-bottom: 65px;
		margin-left: auto;
		margin-right: auto;
		display: inline-block;
		vertical-align: middle;
		@include transition;
		opacity: 0;
		transform: translateY(15px);

		.is-open & {
			opacity: 1;
			transform: translateY(0px);
		}

		.btn-close-modal {
			position: absolute;
			top: 24px;
			right: 24px;
			color: var(--color-text);
			width: 24px;
			height: 24px;
			display: flex;
			justify-content: center;
			align-items: center;
			z-index: 15;
			cursor: pointer;

			svg {
				width: 100%;
				height: auto;
				display: block;
				object-fit: contain;
				pointer-events: none;

				path {
					stroke: currentColor;
				}
			}

			@media (max-width: 767px) {
				top: 12px;
				right: 12px;
			}
		}
	}

	.modal-title {
		text-align: center;
		margin-bottom: clamp-property(24, 32);

		picture, img {
			display: inline;
			width: clamp-property(32, 55);
			height: auto;
			object-fit: contain;
			transform: translateY(-9%) translateX(6%);
			margin-right: 1px;
		}

		@media (max-width: 480px) {
			font-size: clamp-property(26, 48);
		}
	}


	.form-wrap {
		margin-top: clamp-property(32, 40);
	}

	.btn-group {
		justify-content: center;
		margin-top: clamp-property(24, 32);
	}

	.editor {
		max-width: 800px;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
	}


}

.modal__overlay {
	bottom: 0px;
	left: 0px;
	position: fixed;
	right: 0px;
	top: 0px;
	background: rgba(0, 0, 0, 0.6);
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
	overflow-x: hidden;
	backdrop-filter: blur(5px);

}
