@use "../include" as *;

.page-home {
	min-height: min(calc(100vh - 80px), 1271px);

	.section-hero {
		padding-top: clamp-property(120, 200);
		padding-bottom: clamp-property(80, 120);

		.container{
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
		}

		.section-title {
			margin-bottom: 24px;
			text-align: center;

			picture, img {
				display: inline;
				width: clamp-property(32, 55);
				height: auto;
				object-fit: contain;
				transform: translateY(-9%) translateX(6%);
				margin-right: 1px;
			}
		}

		.editor {
			text-align: center;
			max-width: 740px;
			margin-left: auto;
			margin-right: auto;
		}

		.btn-group {
			margin-top: 40px;
			justify-content: center;
			.btn{
				flex: 1;
			}
		}

		.preview-img {
			margin-top: clamp-property(32, 112);
			margin-bottom: clamp-property(32, 80);
			max-width: 740px;
			width: 100%;
			height: auto;
			display: block;
			object-fit: contain;
			margin-left: auto;
			margin-right: auto;
		}

		.preview-text {
			font-family: var(--font-primary);
			font-style: normal;
			font-weight: 700;
			line-height: 1.5;
			text-align: center;
			font-size: clamp-property(24, 32);



		}
	}

	.section-cta-banner {
		padding-bottom: clamp-property(45, 80);

		.cta-banner {
			padding-top: clamp-property(45, 80);
			padding-bottom: clamp-property(45, 98);
			padding-left: 20px;
			padding-right: 20px;
			width: 100%;
			max-width: 1200px;
			background-color: var(--color-dark);
			border-radius: clamp-property(16, 32);
			position: relative;

			.bottom-img {
				position: absolute;
				bottom: 0;
				left: 50%;
				transform: translateX(-50%);
			}

			&__content {
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				text-align: center;
				max-width: 740px;
				margin-left: auto;
				margin-right: auto;
				position: relative;
				z-index: 15;
			}

			.section-title {
				color: var(--color-text-white);
				font-size: clamp-property(32, 56);
				margin-bottom: 24px;
			}

			.editor {
				color: var(--color-text-white);
				font-size: clamp-property(18, 24);
			}

			.btn-group {
				margin-top: 45px;
				justify-content: center;
			}

		}
	}

}
