body {
	min-height: 100vh;
	max-width: var(--max-width);
	width: 100%;
	background-color: var(--bg-color);
	overflow-x: hidden;
}

html {
	accent-color: var(--color-primary);
	overflow-x: hidden;
	max-width: var(--max-width);
}


hr {
	width: 100%;
	display: block;
	border-radius: 100px;
	height: 1px;
	margin: 0;
	border: none;
}

[data-color-scheme="light"] {
	hr {
		background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.1) 49.27%, rgba(0, 0, 0, 0) 100%);
	}
}

[data-color-scheme="dark"] {
	hr {
		background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.15) 49.27%, rgba(255, 255, 255, 0) 100%);
	}
}



